* {
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.navbar {
  width: 100%;
  height: 50px;
  background-color: #000;
  transition: all 1s;
  position: sticky;
  top: 0;
}

.navbar-container {
  height: 100%;
  color: #fff;
  align-items: center;
  padding: 0 50px;
  font-family: Sen, sans-serif;
  display: flex;
}

.logo-container {
  flex: 1;
}

.logo {
  color: #4dbf00;
  font-size: 30px;
}

.menu-container {
  flex: 6;
}

.inputBox {
  width: 100%;
  justify-content: center;
  margin-top: 4rem;
  display: flex;
}

.user-form {
  min-width: 500px;
  align-items: center;
  display: flex;
  position: relative;
}

.search-input {
  width: 100%;
  border: 1px solid #000;
  border-radius: 2rem;
  outline: none;
  padding: 1rem;
  font-size: 1.2rem;
}

.search-btn {
  width: 40px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 2rem;
  padding: 1rem;
  font-size: 1rem;
  position: absolute;
  right: 1rem;
}

.menu-list {
  list-style: none;
  display: flex;
}

.menu-list-item {
  margin-right: 30px;
}

.menu-list-item.active {
  font-weight: bold;
}

.profile-container {
  flex: 2;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.profile-text-container {
  margin: 0 20px;
}

.profile-picture {
  width: 32px;
  height: 32px;
  object-fit: cover;
  border-radius: 50%;
}

.toggle {
  width: 40px;
  height: 20px;
  background-color: #fff;
  border-radius: 30px;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: relative;
}

.toggle-icon {
  color: #daa520;
}

.toggle-ball {
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: #000;
  border-radius: 50%;
  transition: all 1s;
  position: absolute;
  right: 1px;
}

.sidebar {
  width: 50px;
  height: 100%;
  background-color: #000;
  flex-direction: column;
  align-items: center;
  padding-top: 60px;
  transition: all 1s;
  display: flex;
  position: fixed;
  top: 0;
}

.left-menu-icon {
  color: #fff;
  margin-bottom: 40px;
  font-size: 20px;
}

.container {
  min-height: calc(100vh - 50px);
  color: #fff;
  background-color: #151515;
}

.content-container {
  margin-left: 50px;
}

.featured-content {
  height: 50vh;
  padding: 50px;
}

.featured-title {
  width: 200px;
}

.featured-desc {
  width: 500px;
  color: #d3d3d3;
  margin: 30px 0;
}

.featured-button {
  color: #fff;
  background-color: #4dbf00;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 10px 20px;
  font-weight: bold;
}

.movie-list-container {
  padding: 0 20px;
}

.movie-list-wrapper {
  position: relative;
  overflow: hidden;
}

.movie-list {
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
  margin: 2rem 0;
  transition: all 1s ease-in-out;
  display: flex;
}

.movie-list-item {
  margin-right: 30px;
  position: relative;
}

.movie-list-item:hover .movie-list-item-title, .movie-list-item:hover .movie-list-item-desc, .movie-list-item:hover .movie-list-item-button {
  opacity: 1;
}

.movie-list-item-img {
  width: 270px;
  height: 200px;
  object-fit: cover;
  border-radius: 20px;
  transition: all .5s ease-in-out;
}

.movie-list-item-title {
  opacity: 0;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #36363696;
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  transition: all .2s ease-in-out;
  position: absolute;
  top: 10%;
  left: 50px;
}

.movie-list-item-desc {
  opacity: 0;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #3636367e;
  padding: 10px;
  font-size: 14px;
  transition: all .2s ease-in-out;
  position: absolute;
  top: 30%;
  left: 50px;
}

.movie-list-item-button {
  color: #fff;
  cursor: pointer;
  opacity: 0;
  background-color: #4dbf00;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  transition: all .2s ease-in-out;
  position: absolute;
  bottom: 20px;
  left: 50px;
}

.arrow {
  color: #d3d3d3;
  opacity: .5;
  cursor: pointer;
  font-size: 120px;
  position: absolute;
  top: 90px;
  right: 0;
}

.container.active {
  background-color: #fff;
}

.movie-list-title.active {
  color: #000;
}

.navbar-container.active {
  color: #000;
  background-color: #fff;
}

.sidebar.active {
  background-color: #fff;
}

.left-menu-icon.active {
  color: #000;
}

.toggle.active {
  background-color: #000;
}

.toggle-ball.active {
  background-color: #fff;
  transform: translateX(-20px);
}

@media only screen and (max-width: 940px) {
  .menu-container {
    display: none;
  }
}

.search {
  width: 500px;
  height: 30px;
  outline: none;
  font-size: 1.2rem;
}

